// =============================================================================
// RECOMMENDATION APPLIER
// =============================================================================
// Stubbed job that processes pending agent recommendations and applies them
// to facilitator configuration. Currently only logs what it WOULD do.
//
// Recommendation types handled:
// - FACILITATOR_DEPRIORITIZE: Would lower routing weight/priority
// - FACILITATOR_ONBOARD: Would add new facilitator to active roster
// - FACILITATOR_PROMOTE: Would increase routing weight/priority
//
// Future work:
// - Actually mutate facilitator config in database
// - Notify operators via webhook/Slack
// - Track application history for audit trail

import { createLogger } from '@/lib/logger';
import {
  listRecommendations,
  markRecommendationApplied,
} from '@/db/agent-recommendations';
import type { AgentRecommendation } from './types';

const logger = createLogger({ component: 'RecommendationApplier' });

// =============================================================================
// CONFIGURATION
// =============================================================================

export const APPLIER_CONFIG = {
  // Only process recommendations newer than this (hours)
  maxAgeHours: parseInt(process.env.APPLIER_MAX_AGE_HOURS || '24', 10),
  
  // Maximum recommendations to process per run
  batchSize: parseInt(process.env.APPLIER_BATCH_SIZE || '50', 10),
  
  // Whether to actually apply changes (false = dry run / stub mode)
  applyChanges: process.env.APPLIER_APPLY_CHANGES === 'true',
  
  // Priority threshold - only auto-apply critical/high priority
  autoApplyPriorities: ['critical', 'high'] as const,
};

// =============================================================================
// TYPES
// =============================================================================

export interface ApplierResult {
  processed: number;
  applied: number;
  skipped: number;
  errors: number;
  dryRun: boolean;
  recommendations: Array<{
    id: string;
    type: string;
    facilitatorId?: string;
    priority: string;
    action: 'would_apply' | 'applied' | 'skipped' | 'error';
    reason?: string;
  }>;
}

interface GroupedRecommendations {
  [facilitatorId: string]: AgentRecommendation[];
}

// =============================================================================
// MAIN FUNCTION
// =============================================================================

/**
 * Process pending recommendations and apply (or log) them
 * 
 * In stub mode (default), this only logs what it WOULD do.
 * Set APPLIER_APPLY_CHANGES=true to actually mutate config.
 */
export async function applyPendingRecommendations(): Promise<ApplierResult> {
  const startTime = Date.now();
  const isDryRun = !APPLIER_CONFIG.applyChanges;
  
  logger.info({
    maxAgeHours: APPLIER_CONFIG.maxAgeHours,
    batchSize: APPLIER_CONFIG.batchSize,
    dryRun: isDryRun,
    msg: 'Starting recommendation applier',
  });

  // 1. Query pending recommendations
  const since = new Date(Date.now() - APPLIER_CONFIG.maxAgeHours * 60 * 60 * 1000);
  const pendingRecs = await listRecommendations({
    status: 'PENDING',
    since,
    limit: APPLIER_CONFIG.batchSize,
  });

  if (pendingRecs.length === 0) {
    logger.info({ msg: 'No pending recommendations to process' });
    return {
      processed: 0,
      applied: 0,
      skipped: 0,
      errors: 0,
      dryRun: isDryRun,
      recommendations: [],
    };
  }

  logger.info({
    pendingCount: pendingRecs.length,
    msg: 'Found pending recommendations',
  });

  // 2. Group by facilitator ID for deduplication/ordering
  const grouped = groupByFacilitator(pendingRecs);

  // 3. Process each recommendation
  const result: ApplierResult = {
    processed: 0,
    applied: 0,
    skipped: 0,
    errors: 0,
    dryRun: isDryRun,
    recommendations: [],
  };

  for (const rec of pendingRecs) {
    result.processed++;
    
    try {
      const actionResult = await processRecommendation(rec, isDryRun);
      result.recommendations.push(actionResult);
      
      if (actionResult.action === 'applied' || actionResult.action === 'would_apply') {
        result.applied++;
      } else if (actionResult.action === 'skipped') {
        result.skipped++;
      } else if (actionResult.action === 'error') {
        result.errors++;
      }
    } catch (error) {
      const errorMsg = error instanceof Error ? error.message : 'Unknown error';
      logger.error({
        recommendationId: rec.id,
        type: rec.type,
        error: errorMsg,
        msg: 'Failed to process recommendation',
      });
      result.errors++;
      result.recommendations.push({
        id: rec.id,
        type: rec.type,
        facilitatorId: rec.facilitatorId,
        priority: rec.priority,
        action: 'error',
        reason: errorMsg,
      });
    }
  }

  const durationMs = Date.now() - startTime;
  logger.info({
    processed: result.processed,
    applied: result.applied,
    skipped: result.skipped,
    errors: result.errors,
    dryRun: isDryRun,
    durationMs,
    msg: 'Recommendation applier completed',
  });

  return result;
}

// =============================================================================
// HELPERS
// =============================================================================

/**
 * Group recommendations by facilitator ID
 */
function groupByFacilitator(recs: AgentRecommendation[]): GroupedRecommendations {
  const grouped: GroupedRecommendations = {};
  
  for (const rec of recs) {
    const key = rec.facilitatorId || '_no_facilitator';
    if (!grouped[key]) {
      grouped[key] = [];
    }
    grouped[key].push(rec);
  }
  
  return grouped;
}

/**
 * Process a single recommendation
 */
async function processRecommendation(
  rec: AgentRecommendation,
  isDryRun: boolean
): Promise<ApplierResult['recommendations'][0]> {
  // Extract metadata from details for logging
  const metadata = {
    reason: (rec.details as any)?.reason,
    metricsSnapshot: (rec.details as any)?.metricsSnapshot,
    probeStats: (rec.details as any)?.probeStats,
  };

  // Check if this priority should be auto-applied
  const shouldAutoApply = APPLIER_CONFIG.autoApplyPriorities.includes(
    rec.priority as 'critical' | 'high'
  );

  if (!shouldAutoApply) {
    logger.info({
      recommendationId: rec.id,
      type: rec.type,
      facilitatorId: rec.facilitatorId,
      priority: rec.priority,
      msg: 'Skipping recommendation (priority below auto-apply threshold)',
    });
    return {
      id: rec.id,
      type: rec.type,
      facilitatorId: rec.facilitatorId,
      priority: rec.priority,
      action: 'skipped',
      reason: 'Priority below auto-apply threshold',
    };
  }

  // Log what we would do
  logger.info({
    recommendationId: rec.id,
    type: rec.type,
    facilitatorId: rec.facilitatorId,
    priority: rec.priority,
    confidence: rec.confidence,
    reasoning: rec.reasoning,
    metadata,
    dryRun: isDryRun,
    msg: isDryRun ? 'Would apply recommendation (stub)' : 'Applying recommendation',
  });

  // Type-specific handling (all stubbed for now)
  switch (rec.type) {
    case 'FACILITATOR_DEPRIORITIZE':
      await handleDeprioritize(rec, isDryRun);
      break;
      
    case 'FACILITATOR_ONBOARD':
      await handleOnboard(rec, isDryRun);
      break;
      
    case 'FACILITATOR_PROMOTE':
      await handlePromote(rec, isDryRun);
      break;
      
    default:
      logger.info({
        recommendationId: rec.id,
        type: rec.type,
        msg: 'Unhandled recommendation type (no action)',
      });
      return {
        id: rec.id,
        type: rec.type,
        facilitatorId: rec.facilitatorId,
        priority: rec.priority,
        action: 'skipped',
        reason: `Unhandled type: ${rec.type}`,
      };
  }

  // Mark as applied (even in dry run, to prevent reprocessing)
  // In production, you might want to only mark in non-dry-run mode
  if (!isDryRun) {
    await markRecommendationApplied(rec.id, 'recommendation-applier', {
      appliedAt: new Date().toISOString(),
      appliedBy: 'automated',
    });
  }

  return {
    id: rec.id,
    type: rec.type,
    facilitatorId: rec.facilitatorId,
    priority: rec.priority,
    action: isDryRun ? 'would_apply' : 'applied',
  };
}

