// =============================================================================
// RECOMMENDATION APPLIER CRON ENDPOINT
// =============================================================================
// GET /api/cron/recommendation-applier
//
// Processes pending agent recommendations and applies them to facilitator
// configuration. Currently operates in STUB MODE - only logs what it would do.
//
// To enable actual changes, set APPLIER_APPLY_CHANGES=true in environment.
//
// Query params:
// - dryRun=true/false: Override dry run mode (default: true)
//
// Triggered by Vercel Cron or manual invocation

import { NextRequest } from 'next/server';
import { createLogger } from '@/lib/logger';
import { withCronJobTracking, type CronJobContext } from '@/lib/cron-job-wrapper';
import {
  applyPendingRecommendations,
  APPLIER_CONFIG,
} from '@/agents/recommendation-applier';

const logger = createLogger({ component: 'RecommendationApplierCron' });

// =============================================================================
// CRON HANDLER
// =============================================================================

async function applierHandler(
  _request: NextRequest,
  context: CronJobContext
): Promise<{ ok: boolean; [key: string]: any }> {
  const { runId, requestId } = context;

  // Log configuration
  logger.info({
    runId,
    requestId,
    config: {
      maxAgeHours: APPLIER_CONFIG.maxAgeHours,
      batchSize: APPLIER_CONFIG.batchSize,
      applyChanges: APPLIER_CONFIG.applyChanges,
      autoApplyPriorities: APPLIER_CONFIG.autoApplyPriorities,
    },
    msg: 'Starting recommendation applier',
  });

  // Run the applier
  const result = await applyPendingRecommendations();

  logger.info({
    runId,
    requestId,
    processed: result.processed,
    applied: result.applied,
    skipped: result.skipped,
    errors: result.errors,
    dryRun: result.dryRun,
    msg: 'Recommendation applier completed',
  });

  return {
    ok: result.errors === 0,
    
    summary: {
      processed: result.processed,
      applied: result.applied,
      skipped: result.skipped,
      errors: result.errors,
      dryRun: result.dryRun,
    },
    
    // Include recommendation details (limited for response size)
    recommendations: result.recommendations.slice(0, 20).map(r => ({
      id: r.id,
      type: r.type,
      facilitatorId: r.facilitatorId,
      priority: r.priority,
      action: r.action,
      reason: r.reason,
    })),
    
    // Indicate if there are more recommendations not shown
    hasMore: result.recommendations.length > 20,
  };
}

// =============================================================================
// EXPORTS
// =============================================================================

export const dynamic = 'force-dynamic';
export const maxDuration = 60;

export const GET = withCronJobTracking(applierHandler, {
  jobId: 'recommendation-applier',
  timeout: 55000, // 55s (leave buffer for wrapper overhead)
  minInterval: 9 * 60 * 1000, // 9 minutes (schedule is every 10 min)
});

// POST handler for manual triggers
export const POST = withCronJobTracking(applierHandler, {
  jobId: 'recommendation-applier',
  timeout: 55000,
  minInterval: 9 * 60 * 1000,
  skipRateLimit: true, // Allow manual triggers
});
